.text {
  &__title {
    font-size: 20px;
  }

  &__sub-title {
    font-size: 24px;
    font-weight: 900;
  }

  &__section-title {
    font-size: 20px;
    font-weight: bold;
    font-style: italic;
    width: 100%;
    padding: 8px;
    font-weight: 700;
  }

  &__info-box {
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 8px;
    align-items: center;
  }

  &__descriere {
    display: flex;
    width: 100%;
    font-size: 16px;
  }

